          :key="task.id"
          :class="task.done ? 'done' : 'false'"
        >
          <input
            type="checkbox"
            :checked="task.done"
            @change="updateTaskStatus(index)"
          />
          <div class="text">
            <p>{{ task.taskText }}</p>
          </div>
          <font-awesome-icon
            @click="showInfo(index)"
            class="icon info"
            :icon="['fas', 'circle-info']"
          />
          <font-awesome-icon
            @click="apdateTask(index)"
            class="icon"
            :icon="['fas', 'pen-to-square']"
          />
          <button @click="deleteTask(index)" class="delete-button">␡</button>
          <div class="info-task">
            <p>Date: {{ task.date }}</p>
            <p>Time: {{ task.time }}</p>
          </div>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  name: "HomeView",

  data() {
    return {
      id: 0, // id of task
      idToUpdate: "", // id to update text task
      addState: "add", // add button state {add or update}
      inputValue: "", // input value
      tasks: [], // array for all tasks
      date: "", // the date of now
      time: "", //the time when user add task
      arrow: ">", // for button side bar
    };
  },
  methods: {
    // handel add and update button
    add: function () {
      if (this.addState == "update") {
        this.tasks[this.idToUpdate].taskText = this.inputValue;
        this.addState = "add";
        let add = document.querySelector(".add");

        add.textContent = "add";
      } else if (this.addState == "add") {
        // fix
        const date = new Date();
        const day = date.getDate();
        const month = date.getMonth() + 1;
        const year = date.getFullYear();
        const min = date.getMinutes();
        const second = date.getSeconds();
        const hour = date.getHours();
        this.date = `${day}/${month}/${year}`;
        this.time = `${hour}:${min}:${second}`;
        // fix
        this.addState = "add";
        let add = document.querySelector(".add");
        add.textContent = "add";
        if (this.inputValue.length > 0) {
          this.tasks.push({
            id: this.id++,
            taskText: this.inputValue,
            done: false,
            date: this.date,
            time: this.time,
          });
        }
      }
      this.inputValue = "";
    },
    // Handel delete all to show msg
    deleteAllButton() {
      let msg = document.querySelector(".card");
      let overlay = document.querySelector(".overlay");
      overlay.classList.add("show");
      msg.classList.add("show");
    },
    // handel delete all button
    deleteAll() {
      this.tasks = [];
      let msg = document.querySelector(".card");
      let overlay = document.querySelector(".overlay");
      overlay.classList.remove("show");
      msg.classList.remove("show");
      this.inputValue = "";
    },
    // cancel button in msg
    cancelDeleteAll() {
      let msg = document.querySelector(".card");
      let overlay = document.querySelector(".overlay");
      overlay.classList.remove("show");
      msg.classList.remove("show");
    },
    // handel delete task in the li task
    deleteTask(index) {
      this.tasks.splice(index, 1);
      this.inputValue = "";
      this.addState = "add";
      this.$refs.add.textContent = "add";
      // console.log();
    },
    // handel task.done {false or true}
    updateTaskStatus: function (index) {
      this.tasks[index].done = !this.tasks[index].done;
    },
    // handel update task to show text in input
    apdateTask(index) {
      let add = document.querySelector(".add");
      this.idToUpdate = index;
      this.addState = "update";
      add.textContent = "Update";
      this.inputValue = this.tasks[index].taskText;
    },
    // handel show info when click
    showInfo(index) {
      let info = document.querySelectorAll(".info-task");
      let icon = document.querySelectorAll(".info");
      icon[index].classList.toggle("show");
      info[index].classList.toggle("show");
    },
    // show date
    day: function () {
      const daysOfWeek = [
        "Sunday",
        "Monday",
        "Tuesday",
        "Wednesday",
        "Thursday",
        "Friday",
        "Saturday",
      ];
      let date = new Date();
      let day = date.getDate();
      let month = date.getMonth() + 1;
      let year = date.getFullYear();
      let d = daysOfWeek[date.getDay()];
      return `${day} / ${month} / ${year} ${d}`;
    },
    // handel side bar to show or hide
    sideBar: function () {
      let sidebar = document.querySelector(".sidebar");
      sidebar.classList.toggle("open");
    },
    // handel dark mood
    darkMode() {
      let sun = document.querySelector(".sun-and-moon");
      let header = document.querySelector("header");
      sun.classList.toggle("moon");
      sun.classList.toggle("sun");
      if (sun.classList.contains("moon")) {
        localStorage.setItem("mood", "moon");
        document.body.style.backgroundColor = "#0e1c26";
        localStorage.setItem("body", "#0e1c26");

        header.style.backgroundColor = "#2a454b";
        localStorage.setItem("header", "#2a454b");

        this.$refs.logo.style.color = "#fff";
        localStorage.setItem("logo", "#fff");

        this.$refs.tasks.classList.add("dark");
        this.$refs.tasks.classList.remove("light");

        this.$refs.sidebar.style.backgroundColor = "#2e7775";
        localStorage.setItem("sidebar", "#2e7775");

        this.$refs.date.style.backgroundColor = "#0e1c26";
        localStorage.setItem("date", "#0e1c26");

        this.$refs.date.style.color = "#fff";
        localStorage.setItem("date-color", "#fff");

        this.$refs.input.style.backgroundColor = "#08494a";
        localStorage.setItem("input", "#08494a");

        this.$refs.input.style.color = "#fff";
        localStorage.setItem("input-color", "#fff");
      } else {
        localStorage.setItem("mood", "sun");
        document.body.style.backgroundColor = "#efefef";
        localStorage.setItem("body", "#efefef");

        header.style.backgroundColor = "#fff";
        localStorage.setItem("header", "#fff");

        this.$refs.logo.style.color = "#000";
        localStorage.setItem("logo", "#000");

        this.$refs.tasks.classList.add("light");
        this.$refs.tasks.classList.remove("dark");

        this.$refs.sidebar.style.backgroundColor = "#272838";
        localStorage.setItem("sidebar", "#272838");

        this.$refs.date.style.backgroundColor = "#efefef";
        localStorage.setItem("date", "#efefef");

        this.$refs.date.style.color = "#000";
        localStorage.setItem("date-color", "#000");

        this.$refs.input.style.backgroundColor = "#fff";
        localStorage.setItem("input", "#fff");

        this.$refs.input.style.color = "#000";
        localStorage.setItem("input-color", "#000");
      }
    },
  },
  mounted() {
    let sun = document.querySelector(".sun-and-moon");
    localStorage.getItem("mood") == "moon"
      ? (sun.classList.add("moon"),
        sun.classList.remove("sun"),
        this.$refs.tasks.classList.add("dark"),
        this.$refs.tasks.classList.remove("light"))
      : (sun.classList.add("sun"),
        sun.classList.remove("moon"),
        this.$refs.tasks.classList.add("light"),
        this.$refs.tasks.classList.remove("dark"));
    let header = document.querySelector("header");
    document.body.style.backgroundColor = localStorage.getItem("body");

    header.style.backgroundColor = localStorage.getItem("header");

    this.$refs.logo.style.color = localStorage.getItem("logo");

    // this.$refs.tasks.classList.add("light");
    // this.$refs.tasks.classList.remove("dark");

    this.$refs.sidebar.style.backgroundColor = localStorage.getItem("sidebar");

    this.$refs.date.style.backgroundColor = localStorage.getItem("date");

    this.$refs.date.style.color = localStorage.getItem("date-color");
    this.$refs.input.style.backgroundColor = localStorage.getItem("input");
    this.$refs.input.style.color = localStorage.getItem("input-color");
  },
};
</script>

